import Typography from '../atoms/Typography';
import '../../styles/components/_greeting.scss';

const Name = () => (
  <Typography variant="h1" children={'Paul Kim'} className="landing-header" />
);

const GreetingAnimation = ({ mounted }) => {
  const typedHeader = () => {
    switch (mounted) {
      case 2:
        return <Name />;
      case 1:
        return (
          <Typography
            variant="h1"
            children={'My Name is ...'}
            className="landing-header-initial-2"
          />
        );
      case 0:
        return (
          <Typography
            variant="h1"
            children={'Hello'}
            className="landing-header-initial-1"
          />
        );
      default:
        return <Name />;
    }
  };
  return (
    <div className="landing-main">
      {typedHeader()}
      <Typography
        variant="h1"
        children={'Front-end Dev'}
        className={mounted === 4 ? 'job-title-show' : 'job-title'}
      />
    </div>
  );
};

export default GreetingAnimation;
